<template>
<div id="videoPlayer">
  <div class="player">
    <video class="viewer" :src="`/api/video/${id}`" autoplay type="video/mp4" @timeupdate="handleProgressUpdate" @play="handlePlayingEvent" @pause="handlePlayingEvent" @click="handlePlayerClickEvent"></video>
    <div class="controls">
      <div class="progress" @click="scrub">
        <div class="progress-filled" :style="{ flexBasis: `${progress}%` }"></div>
      </div>
      <div class="btn player-button toggle" @click="handlePlayToggle">{{paused ? '►' : '❚ ❚'}}</div>
      <div class="btn player-button time-info">{{time}}</div>
      <div class="btn player-button volume">
        <img @click="showVolume = !showVolume" src="../assets/volume.svg" />
        <input v-if="showVolume" @input="handleRangeUpdate" class="slider" type="range" name="volume" min="0" max="1" step="0.05" />
      </div>
      <div class="btn player-button title">{{title}}</div>
      <div class="btn player-button fullscreen" @click="handleFullscreenToggle">
        <img src="../assets/fullscreen.svg" />
      </div>
    </div>
  </div>

</div>
</template>
<script>
import { pad, humanizeTime } from '../utils/';

export default {
  name: 'VideoPlayer',
  props: ['id'],
  data() {
    return {
      currentTime: 0,
      duration: 0,
      info: {},
      paused: false,
      mousedown: false,
      showVolume: false
    }
  },
  methods: {
    handlePlayerClickEvent() {
      this.handlePlayToggle();
    },
    handleProgressUpdate(e) {
      const video = e.target;
      this.currentTime = video.currentTime;
      this.duration = video.duration;
    },
    handlePlayToggle() {
      const video = this.$el.querySelector('video.viewer');
      video[video.paused ? 'play' : 'pause']();
    },
    handlePlayingEvent(e) {
      const video = e.target;
      this.paused = video.paused;
    },
    handleRangeUpdate(e) {
      const video = this.$el.querySelector('video.viewer');
      video[e.target.name] = e.target.value;
    },
    handleFullscreenToggle() {
      if (document.webkitFullscreenElement) {
        document.webkitExitFullscreen();
      } else {
        this.$el.querySelector('.player').webkitRequestFullscreen();
      }
    },
    scrub(e) {
      const video = this.$el.querySelector('video.viewer');
      video.currentTime = (e.offsetX / this.$el.querySelector('.progress').offsetWidth) * video.duration;
    }

  },
  computed: {
    progress() {
      return (this.currentTime / this.duration) * 100;
    },
    time() {
      const elapsed = humanizeTime(this.currentTime);
      const total = humanizeTime(this.duration);
      return `${elapsed} / ${total}`;
    },
    title() {
      const title = this.info.title;
      const name = this.info.name;
      const seasonEpisode = `S${pad(this.info.season)}E${pad(this.info.number)}`;
      return `${title} - ${seasonEpisode} - ${name}`;
    }
  },
  created() {
    fetch(`/api/video/info/${this.id}`)
      .then(res => res.json())
      .then(res => {
        this.info = res.result.info;
      });
  }
}
</script>

<style>
#videoPlayer {
  display: flex;
  background: #f4f4f4;
  min-height: calc(100vh - 3rem);
  background: linear-gradient(135deg, #7c1599 0%, #921099 50%, #7e4ae8 100%);
  background-size: cover;
  align-items: center;
  justify-content: center;
}

.player {
  max-height: 45rem;
  max-width: 90%;
  font-size: 0;
  border: 2px solid rgba(0, 0, 0, 0.2);
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.2);
  position: relative;
  overflow: hidden;
  cursor: pointer;
}

.player:-webkit-full-screen {
  max-width: none;
  width: 100%;
  border: 0;
}

.viewer {
  width: 100%;
  max-height: auto;
}

.player-button {
  background: none;
  border: 0;
  line-height: 1;
  color: #fff;
  text-align: center;
  outline: 0;
  cursor: pointer;
  transition: 0.4s;
}

.volume {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

.player-button img {
  height: 1.25rem;
  width: 1.25rem;
}

.fullscreen:hover,
.toggle:hover {
  background-color: rgba(255, 255, 255, 0.1);
}

.toggle:focus {
  background-color: rgba(255, 255, 255, 0.1);
}

.player-button.toggle {
  flex: 0 0 3rem;
}

.controls {
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  bottom: 0;
  width: 100%;
  transform: translateY(100%) translateY(-5px);
  transition: 0.3s;
  flex-wrap: wrap;
  background: rgba(0, 0, 0, 0.5);
}

.player:hover .controls {
  transform: translateY(0);
}

.player:hover .progress {
  height: 15px;
}

.controls .title {
  flex: 1;
  text-align: right;
}

.progress {
  flex: 10;
  position: relative;
  display: flex;
  flex-basis: 100%;
  height: 5px;
  transition: height 0.3s;
  background: rgba(0, 0, 0, 0.5);
  border-radius: 0;
  cursor: default;
}

.progress-filled {
  width: 50%;
  background: #00b2ff;
  flex: 0;
  flex-basis: 0%;
}

input[type='range'] {
  background: transparent;
  width: 100%;
  margin: 0 5px;
  appearance: none;
}

input[type='range']:focus {
  outline: none;
  user-select: none;
}

input[type=range]::-webkit-slider-runnable-track {
  width: 100%;
  height: 0.3rem;
  cursor: pointer;
  box-shadow: 1px 1px 1px rgba(0, 0, 0, 0), 0 0 1px rgba(13, 13, 13, 0);
  background: rgba(255, 255, 255, 0.5);
  border-radius: 0.15rem;
  border: 0.2px solid rgba(1, 1, 1, 0);
}

input[type=range]::-webkit-slider-thumb {
  height: 1rem;
  width: 0.5rem;
  border-radius: 5rem;
  background: #f2f2f2;
  cursor: pointer;
  appearance: none;
  margin-top: -0.3rem;
  box-shadow: 0 0 2px rgba(0, 0, 0, 0.2);
}

video::-webkit-media-controls {
  display: none !important;
}

.time-info {
  color: #fff;
}
</style>



