import Vue from 'vue';
import Router from 'vue-router';
import Hello from '@/components/Hello';
import Upload from '@/components/Upload';
import VideoPlayer from '@/components/VideoPlayer';
Vue.use(Router);

export default new Router({
  mode: 'history',
  routes: [{
      path: '/',
      name: 'Hello',
      component: Hello
    },
    {
      path: '/upload',
      name: 'Upload',
      component: Upload
    },
    {
      path: '/video/:id',
      name: 'VideoPlayer',
      component: VideoPlayer,
      props: true
    }
  ]
})



