const pad = n => (n < 10 ? '0' + n : n)

const humanizeTime = t => {
  const hours = (t / 3600) >> 0
  const minutes = ((t - hours * 3600) / 60) >> 0
  const seconds = (t - hours * 3600 - minutes * 60) >> 0
  return `${hours ? hours + ':' : ''}${pad(minutes)}:${pad(seconds)}`
}

const bytesToMb = (bytes) => (bytes * 1e-6);

module.exports = { pad, humanizeTime, bytesToMb };



