<template>
<div class="hello container-fluid">
  <div class="row">
    <div class="col-2" id="showList">
      <ul class="nav flex-column nav-pills">
        <li v-for="(show, index) in Object.keys(shows)" class="nav-item" @click="activeShow = show">
          <a class="nav-link" :class="{active: activeShow === show}">{{show}}</a>
        </li>
      </ul>
    </div>
    <div class="col">
      <Show v-if="activeShow" :title="activeShow" :files="shows[activeShow]"></Show>
    </div>
  </div>
</div>
</template>

<script>
import Show from './Show.vue';

export default {
  name: 'hello',
  components: {
    Show
  },
  data() {
    return {
      files: [],
      activeShow: null
    }
  },
  methods: {},
  computed: {
    shows() {
      const shows = this.files.reduce((acc, curr) => {
        if (acc.hasOwnProperty(curr.info.title)) {
          acc[curr.info.title].push(curr)
        } else {
          acc[curr.info.title] = [curr];
        }
        return acc;
      }, {});
      this.activeShow = Object.keys(shows)[0];
      return shows;
    }
  },
  created() {
    fetch('/api')
      .then(res => res.json())
      .then(res => res.map(file => {
        file.info.summary = file.info.summary.slice(3, file.info.summary.length - 4);
        return file;
      }))
      .then(res => this.files = res);
  }
}
</script>
<style scoped>
#showList {
  color: #fff;
  padding: 0;
  background-color: #1e51b3;
}

.col {
  padding: 0;
}

.nav-link {
  line-height: 2rem;
  border-radius: 0;
  cursor: pointer;
}

.nav-link.active {
  background-color: rgba(255, 255, 255, 0.1);
}

.nav-link:not(.active):hover {
  background-color: rgba(255, 255, 255, 0.05);
}

#showListToggle {
  text-align: right;
}

@media(min-width: 1100px) {
  #showList {
    flex: 1;
    min-height: calc(100vh - 3rem);
  }
}
</style>



